@import url('https://fonts.googleapis.com/css2?family=PT+Mono&display=swap');

$ptMono: 'PT Mono', monospace;

$backgroundColor: #222222;
$bodyColor: #FFFFFF;
$bodyFont: $ptMono,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
}

#content {
  margin: auto;
  width: 50%;
  padding: 16px;
}

a{
  color: $bodyColor;
}

a:hover {
  opacity: 0.6;
}

.no-bull {
  list-style-type: none;
}

.no-bull li {
  margin: 8px 0;
  padding-left: 2em;
  text-indent: -2em;
}
